import React, {useState} from "react";
import {Box, Slide, Tab, Tabs} from "@mui/material";
import {CounterPackScreen} from "./CounterPackScreen";

import {
    TransitionGroup,
    CSSTransition
} from "react-transition-group";


import {HashRouter, Navigate, Route, Routes} from "react-router-dom"

/*import GLogin from "./GLogin";
import GLogout from "./GLogout";*/
import {useTranslation} from "react-i18next";
import {TopPacksScreen} from "./TopPacksScreen";



export function MainScreen (props: any) {

    const location = window.location;
    const mainURL = window.location.pathname.replace(/[#?].*/g,'')
    let pNavIndex=0
    if (location.pathname.includes("counterPack")) {
        pNavIndex=0
    }else if (location.pathname.includes("attackPack")) {
        pNavIndex=1
    }else if (location.pathname.includes("topAttack")) {
        pNavIndex=2
    }else if (location.pathname.includes("topDefence")) {
        pNavIndex=3
    }else if (location.pathname.includes("hydraFire")) {
        pNavIndex=4
    }else if (location.pathname.includes("hydraWater")) {
        pNavIndex=5
    }else if (location.pathname.includes("hydraEarth")) {
        pNavIndex=6
    }else if (location.pathname.includes("hydraWind")) {
        pNavIndex=7
    }else if (location.pathname.includes("hydraLight")) {
        pNavIndex=8
    }else if (location.pathname.includes("hydraDark")) {
        pNavIndex=9
    }
    const [navMenuIndex,setNavMenuIndex] = useState(pNavIndex)
    const [navMenuOpened,setNavMenuOpened] = useState(false)
    const { t,  } = useTranslation();

    const changeMenu = function (event: any, newValue: string) {
        setNavMenuIndex(~~newValue)
    }

    const toggleMenu = function () {
        setNavMenuOpened(!navMenuOpened)
    }


    return (
        <HashRouter>
            <Box className={"mainScreen"}
                 sx={{flexGrow: 1, display: 'flex', width: '100vw', height: '100%', overflowX: 'hidden'}}>

                <Slide direction="right" in={navMenuOpened}>
                    <Box className={"menuBox"}>
                        <Tabs orientation="vertical"
                              variant="scrollable"
                              value={navMenuIndex}
                              onChange={changeMenu}
                              aria-label="Vertical tabs example"
                              sx={{borderRight: 1, borderColor: 'divider'}}>
                            <Tab label={t('WhoWonByTeam')} id={"vertical-tab-0"} aria-controls={"vertical-tabpanel-0"} href={mainURL+"#/counterPack"}/>
                            <Tab label={t('WhoLostByTeam')} id={"vertical-tab-1"} aria-controls={"vertical-tabpanel-1"} href={mainURL+"#/attackPack"}/>
                            <Tab label={t('TopAttack')} id={"vertical-tab-2"} aria-controls={"vertical-tabpanel-2"} href={mainURL+"#/topAttack"}/>
                            <Tab label={t('TopDefence')} id={"vertical-tab-3"} aria-controls={"vertical-tabpanel-3"} href={mainURL+"#/topDefence"}/>
                            <Tab label={t('TopHydraFire')} id={"vertical-tab-4"} aria-controls={"vertical-tabpanel-4"} href={mainURL+"#/hydraFire"}/>
                            <Tab label={t('TopHydraWater')} id={"vertical-tab-5"} aria-controls={"vertical-tabpanel-5"} href={mainURL+"#/hydraWater"}/>
                            <Tab label={t('TopHydraEarth')} id={"vertical-tab-6"} aria-controls={"vertical-tabpanel-6"} href={mainURL+"#/hydraEarth"}/>
                            <Tab label={t('TopHydraWind')} id={"vertical-tab-7"} aria-controls={"vertical-tabpanel-7"} href={mainURL+"#/hydraWind"}/>
                            <Tab label={t('TopHydraLight')} id={"vertical-tab-8"} aria-controls={"vertical-tabpanel-8"} href={mainURL+"#/hydraLight"}/>
                            <Tab label={t('TopHydraDark')} id={"vertical-tab-9"} aria-controls={"vertical-tabpanel-9"} href={mainURL+"#/hydraDark"}/>
                        </Tabs>
                        <Box className={"menuFadeBox"} onClick={toggleMenu}/>
                    </Box>
                </Slide>
                <Box role="tabpanel">
                    <TransitionGroup>
                        {/*
            This is no different than other usage of
            <CSSTransition>, just make sure to pass
            `location` to `Switch` so it can match
            the old location as it animates out.
          */}
                        <CSSTransition
                            key={location.pathname}
                            classNames="fade"
                            timeout={300}
                        >
                    <Routes>
                        <Route path={"/counterPack"} element={
                            <CounterPackScreen openMenuFunction={toggleMenu} mainURL={"/counterPack"} findPackOption={"counter"} key={10}
                            />
                        }/>
                        <Route path={"/attackPack"} element={
                            <CounterPackScreen openMenuFunction={toggleMenu} mainURL={"/attackPack"} findPackOption={"attack"} key={11}
                            />
                        }/>
                        <Route path={"/topAttack"} element={
                            <TopPacksScreen openMenuFunction={toggleMenu} mainURL={"/topAttack"} gMode={"a"} key={0}
                            />
                        }/>
                        <Route path={"/topDefence"} element={
                            <TopPacksScreen openMenuFunction={toggleMenu} mainURL={"/topDefence"} gMode={"d"} key={1}
                            />
                        }/>
                        <Route path={"/hydraFire"} element={
                            <TopPacksScreen openMenuFunction={toggleMenu} mainURL={"/hydraFire"} gMode={"g8010"} key={2}
                            />
                        }/>
                        <Route path={"/hydraWater"} element={
                            <TopPacksScreen openMenuFunction={toggleMenu} mainURL={"/hydraWater"} gMode={"g8011"} key={3}
                            />
                        }/>
                        <Route path={"/hydraEarth"} element={
                            <TopPacksScreen openMenuFunction={toggleMenu} mainURL={"/hydraEarth"} gMode={"g8012"} key={4}
                            />
                        }/>
                        <Route path={"/hydraWind"} element={
                            <TopPacksScreen openMenuFunction={toggleMenu} mainURL={"/hydraWind"} gMode={"g8013"} key={5}
                            />
                        }/>
                        <Route path={"/hydraLight"} element={
                            <TopPacksScreen openMenuFunction={toggleMenu} mainURL={"/hydraLight"} gMode={"g8014"} key={6}
                            />
                        }/>
                        <Route path={"/hydraDark"} element={
                            <TopPacksScreen openMenuFunction={toggleMenu} mainURL={"/hydraDark"} gMode={"g8015"} key={7}
                            />
                        }/>
                        <Route path={"*"} element={
                            <Navigate replace to={mainURL+"counterPack"} />}
                        />
                    </Routes>
                        </CSSTransition>
                    </TransitionGroup>
                </Box>
            </Box>
        </HashRouter>
    )

}