import React, {useState} from "react";
import {Box, Menu, MenuItem, Tooltip} from "@mui/material";
import {IHeroPack} from "../types/IHeroPack";
import {HeroModernIcon} from "./HeroModernIcon";
import {HeroData} from "../types";
import MoreVertIcon from '@mui/icons-material/MoreVert';
import LinkIcon from '@mui/icons-material/Link';
import {useTranslation} from "react-i18next";

function isNN(value:any) {
    return value!==undefined && value !== null
}

function nvl(value:any,newvalue:any) {
    if (isNN(value)) {
        return value
    } else {
        return newvalue
    }
}
function formatK(value:number | undefined | null) {
    if (value!==undefined && value !== null) {
        let result=""
        if (value>1000000) {
            result=(value/1000000).toFixed(1)+"M"
        } else if (value>1000) {
            result=(value/1000).toFixed(1) + "k"
        } else {
            result=""+~~value
        }
        return result //.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")+suffix
    } else {
        return "?"
    }
}

interface IProps {
    heroPack: IHeroPack,
    heroProbs: Map<number, number>,
    onHeroSelect: any,
    selectedHeroes: number[],
    additionalClasses: string[],
    currentPack?: IHeroPack,
    currentInAttack?: boolean
}

interface IState {
    anchorEl: any,
}

export function SelectedHeroPack (props:IProps) {
    const [anchorEl,setAnchorEl] = useState(null)
    const { t } = useTranslation();
    const renderHeroIcons = function () {
        props.heroPack.hIds.sort((a, b) => {
            return (HeroData.getHeroById(a)?.battleOrder || 0) - (HeroData.getHeroById(b)?.battleOrder || 0)
        })
        const items: any = []
        for (let i = 0; i < 5; i++) {
            const hId = props.heroPack.hIds[i]
            items.push(<HeroModernIcon key={i} additionalClasses={["selectedHeroPackIcons"]} heroId={hId}
                                       heroProb={props.heroProbs.get(hId) || 0}
                                       onClick={props.onHeroSelect}/>)
        }
        return items
    }

    const copyLink  = function () {
        navigator.clipboard.writeText(HeroData.translateHeroIdsArray(props.heroPack.hIds))
    }

    const seeCounterPacks  = function (e: any) {

    }

    const handleMenuClick  = function(e: any)  {
        setAnchorEl(e.currentTarget)
    }

    const handleMenuClose  = function (e: any) {
        setAnchorEl(null)
    }

    const renderMenuItems = function () {
        const menuItems :JSX.Element[] = []
        let key = 0
        menuItems.push(<MenuItem key={key++} onClick={seeCounterPacks}>   ( )</MenuItem>)
        /*if (props.heroIdsA !== undefined && props.heroIdsD !== undefined) menuItems.push(<MenuItem key={key++} onClick={seeTrainMode}>Add to Train both packs</MenuItem>)*/
        return menuItems
    }

    
        const additionalClasses = "selectedHeroPack " + props.additionalClasses.join(" ")
        return (
            <Box className={additionalClasses}>
                {props.heroPack?.extendedInfo?.cwr !== undefined && props.heroPack?.extendedInfo?.cc !== undefined &&
                (<Box className={"selectedHeroPackExtendedCurrent"}>
                    <Tooltip title={"   "}><span><img
                        src={"./trophy_a_w.png"}/><span>{props.heroPack.extendedInfo.cwr + "%"}</span></span></Tooltip>
                    <Tooltip title={"   "}><span><img
                        src={"./attack_w.png"}/><span>{props.heroPack.extendedInfo.cc}</span></span></Tooltip>
                </Box>)}
                <Box className={"selectedHeroPackIcons"}>
                    {renderHeroIcons()}
                    <Box className={"linkVerticalBar"}>
                        <Box id="demo-positioned-button"
                             aria-controls="demo-positioned-menu"
                             aria-haspopup="true"
                             aria-expanded={anchorEl ? 'true' : undefined}
                             onClick={handleMenuClick}><Tooltip title={" "}><MoreVertIcon/></Tooltip></Box>
                        <Tooltip title={"   "}><LinkIcon onClick={copyLink}/></Tooltip>
                    </Box>

                </Box>
                <Menu
                    id="demo-positioned-menu"
                    aria-labelledby="demo-positioned-button"
                    anchorEl={anchorEl}
                    open={!!anchorEl}
                    onClose={handleMenuClose}
                    anchorOrigin={{
                        vertical: 'top',
                        horizontal: 'right',
                    }}
                    transformOrigin={{
                        vertical: 'top',
                        horizontal: 'right',
                    }}
                >
                    {renderMenuItems()}
                </Menu>
                <Box className={"selectedHeroPackExtended"}>
                    <span>{t('Total')}:</span>
                    <Tooltip title={" %   "}><span><img alt={""}
                        src={"./trophy_a_w.png"}/><span>{nvl(props.heroPack?.extendedInfo?.wra,"?") + "%"}</span></span></Tooltip>
                    <Tooltip title={"   "}><span><img alt={""}
                        src={"./attack_w.png"}/><span>{nvl(props.heroPack?.extendedInfo?.ca,"?")}</span></span></Tooltip>
                    <Tooltip title={" %   "}><span><img alt={""}
                        src={"./trophy_d_w.png"}/><span>{nvl(props.heroPack?.extendedInfo?.wrd,"?") + "%"}</span></span></Tooltip>
                    <Tooltip title={"   "}><span><img alt={""}
                        src={"./shield_w.png"}/><span>{nvl(props.heroPack?.extendedInfo?.cd,"?")}</span></span></Tooltip>
                </Box>
                {(isNN(props.heroPack?.extendedInfo?.hl) || isNN(props.heroPack?.extendedInfo?.hd) || isNN(props.heroPack?.extendedInfo?.hf) || isNN(props.heroPack?.extendedInfo?.hw) || isNN(props.heroPack?.extendedInfo?.ha) || isNN(props.heroPack?.extendedInfo?.he)) && (
                    <React.Fragment>
                    <Box className={"selectedHeroPackExtended"}>
                        <span>:</span>
                        <Tooltip title={"  "}><span><img alt={""}
                            src={"./hdarkness.png"}/><img  alt={""} src={"./damage.png"}/></span></Tooltip>
                        <span>{formatK(props.heroPack?.extendedInfo?.hd)}</span>
                        <Tooltip title={"  "}><span><img alt={""}
                            src={"./hlight.png"}/><img  alt={""} src={"./damage.png"}/></span></Tooltip>
                        <span>{formatK(props.heroPack?.extendedInfo?.hl)}</span>
                    </Box>
                <Box className={"selectedHeroPackExtended"}>
                    <span>:</span>
                    <Tooltip title={"  "}><span><img alt={""}
                        src={"./hwater.png"}/><img  alt={""} src={"./damage.png"}/></span></Tooltip>
                    <span>{formatK(props.heroPack?.extendedInfo?.hw)}</span>
                    <Tooltip title={"  "}><span><img alt={""}
                        src={"./hwind.png"}/><img  alt={""} src={"./damage.png"}/></span></Tooltip>
                    <span>{formatK(props.heroPack?.extendedInfo?.ha)}</span>
                </Box>
                    <Box className={"selectedHeroPackExtended"}>
                        <span>:</span>
                        <Tooltip title={"  "}><span><img  alt={""} src={"./hearth.png"}/><img  alt={""} src={"./damage.png"}/></span></Tooltip>
                        <span>{formatK(props.heroPack?.extendedInfo?.he)}</span>
                        <Tooltip title={"  "}><span><img  alt={""} src={"./hfire.png"}/><img  alt={""} src={"./damage.png"}/></span></Tooltip>
                        <span>{formatK(props.heroPack?.extendedInfo?.hf)}</span>
                    </Box></React.Fragment>)}
            </Box>
        )
}